.popup__title {
    font-family: 'Inter', Arial, sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    margin: 34px 0 38px 36px;
    align-self: flex-start;
}

@media all and (max-width: 600px) {
    .popup__title {
        font-size: 18px;
        line-height: 22px;
        margin: 25px 0 65px 22px;
    }
}